<template>
  <v-container class="leblanc mb-6">
    <v-row class="mt-16">
      <v-col cols="12">
        <v-data-table
          :headers="headers"
          :items="desserts"
          :loading="loading"
          :footer-props="{
            showFirstLastPage: true,
            itemsPerPageOptions: [5, 10]
          }"
    class="elevation-1"
  >
    <template v-slot:top>
      <v-toolbar
        flat
      >
        <v-toolbar-title>Usuarios</v-toolbar-title>
        <v-divider
          class="mx-4"
          inset
          vertical
        ></v-divider>
        <v-spacer></v-spacer>
        <v-dialog
          v-model="dialog"
          max-width="500px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              color="primary"
              dark
              class="mb-2"
              v-bind="attrs"
              v-on="on"
            >
              Nuevo Usuario
            </v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="text-h5">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-form
                ref="form"
                v-model="valid"
                lazy-validation
              >
              <v-container>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      v-model="editedItem.nombre"
                      label="Nombre"
                      :rules="nombreRules"
                      outlined
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      v-model="editedItem.apellidos"
                      label="Apellidos"
                      :rules="apellidosRules"
                      outlined
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      v-model="editedItem.correo"
                      label="Correo electrnico"
                      :rules="emailRules"
                      outlined
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      v-model="editedItem.nombre_usuario"
                      outlined
                      label="Usuario"
                      :rules="usuarioRules"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-select
                      :items="roles"
                      v-model="editedItem.rol"
                      label="Rol"
                      :rules="[v => !!v || 'Rol es requerido']"
                      outlined
                      required
                    ></v-select>
                  </v-col>
                  <v-col
                    cols="12"
                    sm="6"
                    md="6"
                  >
                    <v-text-field
                      v-model="editedItem.contrasea"
                      outlined
                      label="Contrasea"
                      required
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
              </v-form>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                color="secondary darken-1"
                text
                @click="close"
              >
                Cancelar
              </v-btn>
              <v-btn
                :disabled="!valid"
                color="primary darken-1"
                text
                @click="save"
              >
                Guardar
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
        <v-dialog v-model="dialogDelete" max-width="500px">
          <v-card>
            <v-card-title class="text-h5">Desea eliminar este registro?</v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="closeDelete">NO</v-btn>
              <v-btn color="blue darken-1" text @click="deleteItemConfirm">SI</v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-icon
        small
        class="mr-2"
        @click="editItem(item)"
      >
        fas fa-edit
      </v-icon>
      <v-icon
        small
        @click="deleteItem(item)"
      >
        fas fa-trash
      </v-icon>
    </template>
  </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
import axios from "axios";
import { mapMutations } from "vuex";
import { apiURL } from "../../util/api";
export default {
  name: "Users",
  components: {
  },
  data: () => ({
    valid: true,
    dialog: false,
    loading: false,
    dialogDelete: false,
    // DataTable Paginacin
    totalDesserts: 0,
    desserts: [],
    roles: [ 'Usuario', 'Admin'],
    options: {
      itemsPerPage: 10
    },
    headers: [
    {
      text: "Nombre",
      align: "start",
      sortable: false,
      value: "nombre"
    },
    { text: "Apellidos", value: "apellidos", sortable: false },
    {
      text: "Correo electrnico",
      value: "correo",
      sortable: false
    },
    { text: "Usuario", value: "nombre_usuario", sortable: false },
    { text: "Rol", value: "rol", sortable: false },
    { text: "Acciones", value: "actions", sortable: false }
    ],
    editedIndex: -1,
    editedItem: [],
    nombreRules: [
      v => !!v || 'Nombre es requerido',
      v => (v && v.length >= 3) || 'Nombre debe tener ms de 3 caracteres',
    ],
    apellidosRules: [
      v => !!v || 'Apellidos es requerido',
      v => (v && v.length >= 3) || 'Apellidos debe tener ms de 3 caracteres',
    ],
    usuarioRules: [
      v => !!v || 'Usuario es requerido',
      v => (v && v.length >= 3) || 'Usuario debe tener ms de 3 caracteres',
    ],
    emailRules: [
      v => !!v || 'Correo electrnico requerido',
      v => /.+@.+\..+/.test(v) || 'Correo electrnico debe ser vlido',
    ],
    passRules: [
      v => !!v || 'Contrasea es requerida',
      v => (v && v.length >= 6) || 'Contrasea debe tener ms de 6 caracteres',
    ],
    deleteItemU: null
  }),
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'Nuevo Usuario' : 'Editar Usuario'
    },
  },
  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogDelete (val) {
      val || this.closeDelete()
    },
  },
  methods: {
    ...mapMutations(["ocultarLoad", "mostrarLoad"]),
    getUsers() {
      this.loading = true
      // const { page } = this.options;
      axios
      .get(`${apiURL}getUsers`, {
        
      })
      .then(response => {
        // console.log(response.data.data[0]);
        Object.keys(response.data.data[0]).forEach(element => {
      // console.log( response.data.data[0][element].nombre);
      this.desserts.push({
        nombre: response.data.data[0][element].nombre,
        apellidos: response.data.data[0][element].apellidos,
        nombre_usuario: response.data.data[0][element].nombre_usuario,
        correo: response.data.data[0][element].correo,
        rol: response.data.data[0][element].rol,
        id: response.data.data[0][element]._id
      });
    });
        // this.totalDesserts = response.data.data.count;
        //this.desserts = Object.values(response.data.data[0][0]);
        this.loading = false;
      })
      .catch(error => {
        console.error(error);
        this.loading = false;
        this.$toast.open({
          message:
          "No se pudo procesar la informacin, por favor intentelo ms tarde!",
          type: "error",
          position: "bottom-right",
          duration: 5000
        });
      });
    },
     editItem (item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      // console.log(item.id)
      this.deleteItemU = item.id
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    async deleteItemConfirm () {
      /*console.log(this.editedIndex)
      this.desserts.splice(this.editedIndex, 1)
      this.closeDelete()*/
      this.mostrarLoad({ titulo: "Por favor espere" });
      let resultados = await axios.delete(`${apiURL}deleteUser`, {
        data: { id: this.deleteItemU }
      });
      console.log(resultados.data.data[0])
      if (resultados.data.data[0] === "Usuario eliminado") {
        this.getUsers();
        this.ocultarLoad();
        this.closeDelete()
        this.$toast.open({
          message: "Registro eliminado con xito!",
          type: "success",
          position: "bottom-right",
          duration: 5000
        });
      } else {
        this.ocultarLoad();
        this.$toast.open({
          message: "Disculpe ocurrio un error!",
          type: "error",
          position: "bottom-right",
          duration: 5000
        });
      }
    },

    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    closeDelete () {
      this.dialogDelete = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    async save () {
      this.$refs.form.validate()
      /*if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem)
      } else {
        this.desserts.push(this.editedItem)
      }
      this.close()*/
      if(this.valid) {
        try {
        this.mostrarLoad({ titulo: "Por favor espere" });
        await axios.post(`${apiURL}createUser`, {
          nombre: this.editedItem.nombre,
          apellidos: this.editedItem.apellidos,
          nombre_usuario: this.editedItem.nombre_usuario,
          correo: this.editedItem.correo,
          contrasea: this.editedItem.contrasea,
          rol: this.editedItem.rol,
          // motivo: this.motivo
        });
        this.ocultarLoad();
        this.editedItem = []
        this.getUsers()
        this.close()
        } catch (ex) {
      console.error("inner", ex.message);
      this.ocultarLoad();
      this.$toast.open({
        message:
          "No se pudo procesar la informacin, por favor intentelo ms tarde!",
        type: "error",
        position: "bottom-right",
        duration: 5000
      });
    } finally {
      this.ocultarLoad();
      console.log("finally");
    }
      }
    },

  },
  mounted() {
    this.getUsers()
  }
}
</script>