import Vue from 'vue'
import sessionStorage from 'vue-sessionstorage/src/sessionStorage'
import VueRouter from 'vue-router'
import PageNotFound from '../components/404'
import Noactiva from '../views/Noactiva.vue'
import Registro from '../views/Registro.vue'
import Registro02 from '../views/Registro2.vue'
import IndexAdmin from '../views/admin/Index.vue'
import Busqueda from '../views/admin/Busqueda.vue'
import User from '../views/admin/User.vue'

Vue.use(VueRouter)

const routes = [
{
    path: '/',
    name: 'Noactiva',
    component: Noactiva
  },
  {
    path: '/registro',
    name: 'Registro',
    component: Registro
  },
  {
    path: '/registro02/:eleccion_id/:codigo/:nivel/:id_folio',
    name: 'Registro02',
    component: Registro02,
    props: true
  },
  {
    path: '/registro_admin',
    name: 'IndexAdmin',
    component: IndexAdmin,

  },
  {
      path: '/busqueda',
      name: 'Busqueda',
      component: Busqueda,
      meta: { auth: true}
    },
    {
      path: '/usuarios',
      name: 'User',
      component: User,
      meta: { auth: true}
    },
  /* {
    path: '/home',
    name: 'Home',
    component: Home
  }, */
  // {
  //  path: '/about',
  //  name: 'About',
  //  component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  // }
  { path: "*", component: PageNotFound }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

const session = new sessionStorage()

router.beforeEach((to, from, next) => {
  const authRuta = to.matched.some(item => item.meta.auth)
  if(authRuta && session.exists("token") === false) {
    next('registro_admin')
  } else {
    next()
  }
})

export default router
