<template>
  <v-app>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <div class="d-flex align-center">
        <router-link to="/">
        <v-img
          alt="Federacion de Estudiantes Universitarios"
          class="shrink mr-2"
          contain
          src="@/assets/feuwhite.png"
          transition="scale-transition"
          width="40"
        />
        </router-link>
        <!-- <v-img
          alt="Vuetify Name"
          class="shrink mt-1 hidden-sm-and-down"
          contain
          min-width="100"
          src="https://cdn.vuetifyjs.com/images/logos/vuetify-name-dark.png"
          width="100"
        /> -->
      </div>
      <v-spacer></v-spacer>
      <v-toolbar-title  v-if="this.$route.name === 'Registro'">REGISTRO</v-toolbar-title>
      <v-spacer></v-spacer>

      <v-btn v-if="this.$route.name === 'Noactiva'"
        to="/registro"
        plain
      >
        <span class="mr-2" v-if="primera_vez">Registro de Candidatos</span>
        <span class="mr-2" v-else>Registro de COALICIONES</span>
      </v-btn>
     <v-btn v-if="this.$route.name === 'Noactiva'"
        to="/registro_admin"
        plain
      >
        <span class="mr-2">ADMIN</span>
      </v-btn>

       <v-menu offset-y v-if="(this.$route.name === 'Busqueda' || this.$route.name === 'User') && user.rol === 'Admin'">
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          plain
          v-bind="attrs"
          v-on="on"
        >
          Configuracin
        </v-btn>
      </template>
      <v-list>
        <v-list-item nav to="/busqueda" exact>
          <v-list-item-title>Bsqueda</v-list-item-title>
        </v-list-item>
        <v-list-item nav>
          <v-list-item-title @click="enventoCambioFase">Cambio fase</v-list-item-title>
        </v-list-item>
        <v-list-item nav to="/usuarios" exact>
          <v-list-item-title>Usuarios</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>

      <v-btn v-if="this.$route.name === 'Busqueda' || this.$route.name === 'User'"
        @click="cerrarSession"
        plain
      >
        <span class="mr-2">CERRAR SESIN</span>
      </v-btn> 
    </v-app-bar>

    <v-main class="gris">
      <v-container fluid>
      <router-view/>
      <v-dialog
      v-model="loading.estado"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          {{loading.titulo}}
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <cambio-fase></cambio-fase>
      </v-container>
    </v-main>
    <app-footer v-if="this.$route.name !== 'Registro02'"></app-footer>
    <app-footer2 v-if="this.$route.name === 'Registro02'"></app-footer2>
  </v-app>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";
import eventBus from "./event-bus";
import {apiURL} from './util/api'
import Footer from "./components/Footer.vue";
import Footer2 from "./components/Footer2.vue";
import CambioFase from './components/ModalCambioFase.vue'
export default {
  name: 'App',
components: {
    "app-footer": Footer,
    "app-footer2": Footer2,
    CambioFase
  },
  mounted: function () {
    //console.log(this.$route)
    this.getPrimeraVez();
    if(!this.user) {
      this.user = this.$session.get("user")
    }
  },
  data: () => ({
    primera_vez: null,
    user: null
  }),
  computed: {
    ...mapState(['loading'])
  },
  methods: {
    async getPrimeraVez() {
      let first = await axios.get(
        `${apiURL}getFirst`
      );
      this.loading = false;
      // console.log(first.data.data[0][0].primera_vez)
      this.primera_vez = first.data.data[0][0].primera_vez;
    },
    cerrarSession() {
      this.$session.clear('token')
      this.$session.clear('user')
      this.$router.push({ name: 'IndexAdmin'});
    },
    // Modal Cambio de Fase
    enventoCambioFase() {
      eventBus.$emit('ModalCambioFase',{dialog: true});
    }
  }
};
</script>
