<template>
  <v-container class="leblanc mb-6">
    <v-row no-gutters>
      <v-col
        cols="12"
        sm="12"
        md="12"
      >
        <v-img
        :src="require('../../assets/logo_FEU_gray@2x.png')"
        class="my-3 mt-16"
        contain
        height="130"
        />
      </v-col>
      <v-col
        cols="12"
        sm="12"
        md="12"
      >
        <p class="font-weight-medium text-center text-h6 mt-5">
      Ingrese su usuario y contrasea
    </p>
      </v-col>
      <v-col
        cols="12"
        sm="12"
        md="12"
      >
      <v-row no-gutters>
      <v-col
        cols="4"
        sm="4"
        md="4"
      >
        
      </v-col>
      <v-col
        cols="4"
        sm="4"
        md="4"
      >
       <v-form
    ref="form"
    v-model="valid"
    lazy-validation
    class="mb-15"
  >
    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="Usuario"
      outlined
      required
      placeholder="Correo electrnico"
    ></v-text-field>

    <v-text-field
      v-model="password"
      :rules="passwordRules"
      label="Contrasea"
      type="password"
      placeholder="Contrasea"
      outlined
      required
    ></v-text-field>

    <v-btn
      :disabled="!valid"
      color="primary"
      class=""
      @click="validate"
    >
      INGRESAR
    </v-btn>
  </v-form> 
      </v-col>
      <v-col
        cols="4"
        sm="4"
        md="4"
      >
        
      </v-col>
    </v-row>
    </v-col>
    </v-row>
</v-container>
</template>

<script>
  import axios from "axios";
  import {apiURL} from '../../util/api'
export default {
  name: 'Index',
  data: () => ({
    valid: true,
      password: '',
      passwordRules: [
        v => !!v || 'Contrasea es requerida',
        v => (v && v.length >= 8) || 'Minmo 8 caracteres',
      ],
      email: '',
      emailRules: [
        v => !!v || 'Correo electrnico es requerido',
        v => /.+@.+\..+/.test(v) || 'Correo electrnico debe ser vlido',
      ],
  }),
  mounted() {
    /* let existe = this.$session.exists("token")
    if(existe) {
      this.$session.clear("token")
      this.$session.clear("user")
    } */
  },
  methods: {
      async validate () {
        this.$refs.form.validate()
        //this.$router.push({ name: 'Busqueda'})
        this.valid = false
        let respuesta = await axios.post(
        `${apiURL}login`,
          {
            correo: this.email,
            contrasea: this.password,
          }
        );
        //console.log(respuesta.data);
        this.$session.set('token', respuesta.data.token);
        this.$session.set('user', respuesta.data.user);
        this.$router.push({ name: 'Busqueda'});
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
    },
};
</script>
