<template>
  <div>
    
      <v-dialog
        v-model="dialog"
        persistent
        max-width="800px"
        max-height="1200px"
      >
        <v-card>
          <v-card-title class="text-h6 primary white--text">
            Documento
            <v-spacer></v-spacer
            ><v-icon color="white" @click="cerrarModalActual">fas fa-times</v-icon>
          </v-card-title>
          <v-card-text>
            <template v-if="tipoArchivo === 'data:image'">
            <v-img
            contain
          :src="dataUrl"
          aspect-ratio="1.7"
        ></v-img>
        </template>
        <template v-else>
          <v-row>
            <v-col cols="12" md="12" class="text-center mt-2">
            <v-btn
              icon
              color="pink"
              large
              :disabled="DbtnA"
              @click="backP"
            >
              <v-icon>fas fa-arrow-left</v-icon>
            </v-btn>
            <v-chip
              class="ma-2"
              color="primary"
            >
              {{currentPage}} / {{pageCount}}
            </v-chip>
          <v-btn
              icon
              color="pink"
              large
              :disabled="DbtnAd" 
              @click="nextP"
            >
              <v-icon>fas fa-arrow-right</v-icon>
            </v-btn>
          </v-col>
        <v-col cols="12" md="12">
          <pdf :src="dataUrl" @num-pages="pageCount = $event"
          @page-loaded="currentPage" :page="currentPage" style="display: block;"></pdf>
          </v-col>
        </v-row>
        </template>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              text
              @click="cerrarModalActual"
            >
              Cerrar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
  </div>
</template>

<script>
  import pdf from 'vue-pdf'
  export default {
    name: "DialogDocumento",
    components: {
      pdf
    },
    props: {
    dialog: Boolean,
    dataUrl: String,
    tipoArchivo: String
    },
    data () {
      return {
        loading: false,
        currentPage: 1,
        pageCount: 0,
        DbtnAd: false,
        DbtnA: false
      }
    },
    methods: {
      cerrarModalActual() {
        this.$emit(newFunction(), false)

        function newFunction() {
          return 'cerrarModalDialogDocumento'
        }
      },
      // Paginado
      nextP() {
        if(this.currentPage < this.pageCount) {
          this.currentPage += 1
          this.DbtnAd = false
        } else {
          this.DbtnAd = true
        }
      },
      backP() {
        if(this.currentPage > 1) {
          this.currentPage -= 1
          this.DbtnA = false
        } else {
          this.DbtnA = true
        }
      }
      
    },
    mounted() {

  },
  }
</script>
