<template>
  <div class="text-center">
    <v-dialog v-model="dialog" width="500">
      <v-card>
        <v-card-title class="text-h5 primary white--text">
          Cambio de fase
        </v-card-title>

        <v-card-text>
          <v-switch
            v-model="primera_vez"
            :label="`Primera fase: ${primera_vez.toString()}`"
            @change="updatePrimeraVez"
          ></v-switch>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false">
            cerrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { apiURL } from "../util/api";
import eventBus from "./../event-bus";
export default {
  data() {
    return {
      loading: false,
      dialog: false,
      primera_vez: false
    };
  },
  methods: {
    openModal(data) {
      this.dialog = data.dialog;
    },
    async getPrimeraVez() {
      this.loading = true;
      let first = await axios.get(`${apiURL}getFirst`);
      this.loading = false;
      // console.log(first.data.data[0][0].primera_vez)
      this.primera_vez = first.data.data[0][0].primera_vez;
    },
    async updatePrimeraVez(){
      this.loading = true;
      console.log(this.primera_vez)
      await axios.put(`${apiURL}updateFirst`, {
              id: "6199887d45ccd4e8fe18e339",
              primera_vez: this.primera_vez
      });
      this.loading = false;
      // console.log(first.data.data[0][0].primera_vez)
    }
  },
  mounted() {
    eventBus.$on("ModalCambioFase", data => {
      this.openModal(data); // {name:'foo'}
    });
    this.getPrimeraVez();
  }
};
</script>