/* Cach de datos de pgina */
var _CACHE_OBJS = {};
function _init_cache(comp, key, cache) {
  var obj = cache[key];
  if (obj !== undefined) {
    comp[key] = obj;
  }
  var deep = typeof comp[key] === 'object';
  comp.$watch(key,
    function (val) {
      //console.log("page " + key + " updated");
      cache[key] = val;
    }, {
      deep: deep
    });
}
var _PAGE_CACHE = {
  /*
       * Inicializar los datos de la cach de la pgina
       * comp: objeto del componente de la pgina actual
       * ruta: ruta del enrutador vue de la pgina actual
       * datos: el nombre del objeto de datos que se almacenar en cach, o una matriz de nombres
   */
  cache: function (comp, path, data) {
    if (data == '' || data == undefined || data == null) {
      data = restore(comp._data);
    }
    var cache = _CACHE_OBJS[path];
    if (cache === undefined) {
      cache = {};
      _CACHE_OBJS[path] = cache;
    }
    if (typeof data == 'string') {
      _init_cache(comp, data, cache);
    } else {
      var i;
      for (i = 0; i < data.length; ++i) {
        _init_cache(comp, data[i], cache);
      }
    }
         console.log (_CACHE_OBJS, "Cach de datos de pgina");
  },
     /* Borrar cach de pgina */
  clear: function (path) {
    delete _CACHE_OBJS[path];
  },
     /* Borrar todos los datos en cach */
  reset: function () {
    //console.log("reset page cache");
    _CACHE_OBJS = {};
  },
     /* Verifique si la cach de la pgina actual existe de acuerdo con la ruta */
  has_cache: function (path) {
    return _CACHE_OBJS[path] !== undefined && !isEmptyObject(_CACHE_OBJS[path]);
  }
};
Vue.prototype.$cache = _PAGE_CACHE;
/* eslint-disable no-new */
var restore = function (vueObject) {
  var result = [];
  for (var index in vueObject) {
    result.push(index);
  }
  return result;
};
var isEmptyObject = function (obj) {
  for (var key in obj) {
    return false;
  }
  return true;
}

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
