    ],
    codigoUGRules: [
      (v) => !!v || "Cdigo U de G es requerido",
      (v) =>
        (v && v.length >= 5) || "Cdigo U de G debe tener ms de 2 caracteres",
      (v) => /^\d*$/.test(v) || "Solo nmeros",
    ],
    emailRules: [
      (v) => !!v || "Correo electrnico es requerido",
      (v) => /.+@.+/.test(v) || "Correo electrnico debe ser vlido",
    ],
    selectRules: [(v) => !!v || "Campo requerido"],
  }),
  methods: {
    ...mapMutations(["ocultarLoad", "mostrarLoad"]),
    async getSexo() {
      this.loading = true;
      let sexo = await axios.get(
        `${apiURL}obtainsex`
      );
      this.loading = false;
      let sx = sexo.data.data;
      Object.keys(sx).forEach((element) => {
        //console.log( sx[element].sexo);
        this.sexos.push(sx[element].sexo);
      });
    },
    async getGenero() {
      this.loading = true;
      let genero = await axios.get(
        `${apiURL}obtaingender`
      );
      this.loading = false;
      let gn = genero.data.data;
      Object.keys(gn).forEach((element) => {
        //console.log( gn[element].genero);
        this.generos.push(gn[element].genero);
      });
    },

    async getTurnos() {
      this.loading = true;
      let turno = await axios.get(
        `${apiURL}obtainturns`
      );
      this.loading = false;
      let tn = turno.data.data;
      Object.keys(tn).forEach((element) => {
        //console.log( gn[element].genero);
        this.turnos.push(tn[element].turno);
      });
    },

    async postObtenerCentrosUniversitarios() {
      this.loading = true;
      let respuesta = await axios.post(
        `${apiURL}obtaincentros`,
        {
          eleccion_id: this.eleccion_id,
        }
      );
      this.loading = false;
      let lista_centros = respuesta.data.data;
      Object.keys(lista_centros).forEach((element) => {
        //console.log( lista_niveles[element].nivel);
        this.centros.push({
          value: lista_centros[element].centro,
          label: lista_centros[element].centro_desc,
        });
      });
      //Se comprueba si es preparatoria
      if (this.nivel_presidente === "Preparatoria") {
        this.centros = this.centros.filter(function (centro) {
          return centro.value === "SEMS";
        });
      } else {
        this.centros = this.centros.filter(function (centro) {
          return centro.value !== "SEMS";
        });
      }
    },

    async postObtenerDivisiones() {
      this.loading = true;
      this.divisiones = [];
      let respuesta = await axios.post(
        `${apiURL}obtaindivision`,
        {
          eleccion_id: this.eleccion_id,
          centro: this.centro_presidencia,
        }
      );
      this.loading = false;
      let lista_divisiones = respuesta.data.data;
      Object.keys(lista_divisiones).forEach((element) => {
        //console.log( lista_niveles[element].nivel);
        this.divisiones.push({
          value: lista_divisiones[element].plantel,
          label: lista_divisiones[element].plantel,
        });
      });
    },

    async getGruposPoliticos() {
      this.loading = true;
      let grupospoliticos = await axios.get(
        `${apiURL}obtainpoliticalgroups`
      );
      this.loading = false;
      let gp = grupospoliticos.data.data;
      Object.keys(gp).forEach((element) => {
        //console.log( gp[element]);
        this.grupos_politicos.push({
          value: gp[element].id,
          label: gp[element].nombre,
        });
      });
      this.grupos_politicos = this.grupos_politicos.sort(function (a, b) {
        if (a.label > b.label) {
          return 1;
        }
        if (a.label < b.label) {
          return -1;
        }
        // a must be equal to b
        return 0;
      });
    },

    async postObtenerCarreras() {
      this.loading = true;
      this.carreras = [];
      this.vicepresidencia_ejecutiva = [];
      let respuesta = await axios.post(
        `${apiURL}obtaincarrera`,
        {
          eleccion_id: this.eleccion_id,
          centro: this.centro_presidencia,
          plantel: this.division_presidencia,
        }
      );
      this.loading = false;

      let lista_carreras = respuesta.data.data;
      Object.keys(lista_carreras).forEach((element) => {
        //console.log( lista_niveles[element].nivel);
        this.vicepresidencia_ejecutiva.push({nombre: "",carrera: "",turno: "",semestre: "",codigo: "",sexo: "",genero: "",files_credencial: [],files_credencialB64: "",files_kardex: [],files_kardexB64: "",
        });
        this.carreras.push({
          value: lista_carreras[element].carrera,
          label: lista_carreras[element].carrera,
        });
      });
      this.carreras = this.carreras.sort(function (a, b) {
        if (a.label > b.label) {
          return 1;
        }
        if (a.label < b.label) {
          return -1;
        }
        // a must be equal to b
        return 0;
      });
    },
    //SECCION para guardar la informacion suministrada en el formulario
    validate() {
      this.$refs.form.validate();
    },
    async postGuardar() {
      let valido = this.$refs.form.validate();
      if (valido) {
        try {
          let url
          if(this.primera_vez) {
            url = `${apiURL}registerStep2`
          } else {
            url = `${apiURL}registerFaseDosStep2`
          }
          this.mostrarLoad({ titulo: "Enviando datos, por favor espere" });
          let respuesta = await axios.post(url, {
            folio: this.id_folio,
            nombre_presidente: this.nombre_presidente,//string
            corriente_politica_presidente: this.corriente_politica_presidente,//strig
            semestre_presidente: this.semestre_presidente,//string
            sexo_presidente: this.sexo_presidente,//string
            genero_presidente: this.genero_presidente,//string
            curp_presidente: this.curp_presidente,//string
            nivel_presidente: this.nivel_presidente,//string
            codigo_presidente: this.codigo_presidente,//string
            centro_presidencia: this.centro_presidencia,//string
            division_presidencia: this.division_presidencia,//string
            carrera_presidencia: this.carrera_presidencia,//string
            turno_presidente: this.turno_presidente,//string
            email_presidencia: this.email_presidencia,//string
            facebook_presidencia: this.facebook_presidencia,//string
            facebook_grupo_presidencia: this.facebook_grupo_presidencia,//string
            twitter_presidencia: this.twitter_presidencia,//string
            instagram_presidencia: this.instagram_presidencia,//string
            snapchat_presidencia: this.snapchat_presidencia,//string
            //Files Presidencia
            files_fotografia: this.files_fotografiaB64,//string
            files_credencial: this.files_credencialB64,//array
            files_plan_trabajo: this.files_plan_trabajoB64,//array
            files_carta_no_violencia: this.files_carta_no_violenciaB64,//array
            files_kardex: this.files_kardexB64,//array
            files_planilla_urna_votacion: this.files_planilla_urna_votacionB64,//array
            files_carta_militancia: this.files_carta_militanciaB64,//array
            nombre_alias_presidencia: this.nombre_alias_presidencia,//string
            apodo_presidencia: this.apodo_presidencia,//string
            equipo_presidente: this.equipo_presidente,//string
            //Datos Vicepresidencia
            nombre_vicepresidencia: this.nombre_vicepresidencia,//string
            carrera_vicepresidencia: this.carrera_vicepresidencia,//string
            turno_vicepresidencia: this.turno_vicepresidencia,//string
            semestre_vicepresidencia: this.semestre_vicepresidencia,//string
            codigo_vicepresidencia: this.codigo_vicepresidencia,//string
            sexo_vicepresidencia: this.sexo_vicepresidencia,//string
            genero_vicepresidencia: this.genero_vicepresidencia,//string
            grupo_politico_vicepresidencia: this.grupo_politico_vicepresidencia,//string
            //Vicepresidencia ejecutiva
            vicepresidencia_ejecutiva: this.vicepresidencia_ejecutiva,//array in array
            //Secretaria
            nombre_secretaria: this.nombre_secretaria,//string
            carrera_secretaria: this.carrera_secretaria,//string
            turno_secretaria: this.turno_secretaria,//string
            semestre_secretaria: this.semestre_secretaria,//string
            codigo_secretaria: this.codigo_secretaria,//string
            sexo_secretaria: this.sexo_secretaria,//string
            genero_secretaria: this.genero_secretaria,//string
            grupo_politico_secretaria: this.grupo_politico_secretaria//string
          });
          if(!respuesta.data.response) {
            this.$toast.open({
                message: respuesta.data.message.toUpperCase(),
                type: 'error',
                position: 'bottom-right',
                duration: 5000,
            });
            
          } else {
            eventBus.$emit('guardado',{dialog: true, folio: this.id_folio});
          }
          //eventBus.$emit("guardado", { dialog: true, folio: "123456789" });
        } catch (ex) {
          console.error("inner", ex.message);
          this.ocultarLoad();
          this.$toast.open({
          message: 'No se pudo procesar la informacin, por favor intentelo ms tarde!',
          type: "error",
          position: "bottom-right",
          duration: 5000,
        });
        } finally {
          this.$refs.form.resetValidation();
          this.ocultarLoad();
          console.log("finally");
        }
      } else {
        //this.$refs.form.resetValidation()
        this.$toast.open({
          message: "LOS CAMPOS NO PUEDEN ESTAR VACIOS!",
          type: "error",
          position: "bottom-right",
          duration: 5000,
        });
      }
    },
    //CONVERTIR EN BASE64

    //fotografia
    handlerFotoP(value) {
      if (value[0].size < 500000) {
        this.createBase64Foto(value[0].file);
      } else {
        this.files_fotografia = []
        this.$toast.open({
          message: "Su archivo excede los 500 KB!",
          type: "warning",
          position: "bottom-right",
          duration: 5000,
        });
      }
      
    },
    createBase64Foto(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.files_fotografiaB64 = e.target.result;
      };
      reader.readAsDataURL(file);
    },

    //credencial
    handlerCredeP(value) {
      if (value[0].size < 500000) {
        this.createBase64Crede(value[0].file);
      } else {
        this.files_credencial = []
        this.$toast.open({
          message: "Su archivo excede los 500 KB!",
