    //urna  de votacion
    handlerUrnaVotoP(value) {
      if (value[0].size < 500000) {
        this.createBase64UrnaVoto(value[0].file);
      } else {
        this.files_planilla_urna_votacion = []
        this.$toast.open({
          message: "Su archivo excede los 500 KB!",
