      }
    },
    createBase64CartaMili(file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.files_carta_militanciaB64 = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    //SECCION de los metodos para eliminar los archivos seleccionados previamente en el formulario
    deleteFilesFotografia() {
      this.files_fotografia = [];
      this.files_fotografiaB64 = null;
    },
    deleteFilesCredencial() {
      this.files_credencial = [];
      this.files_credencialB64 = null;
    },
    deleteFilesPlanTrabajo() {
      this.files_plan_trabajo = [];
      this.files_plan_trabajoB64 = null;
    },
    deleteFilesCartaNoViolencia() {
      this.files_carta_no_violencia = [];
      this.files_carta_no_violenciaB64 = null;
    },
    deleteFilesKardex() {
      this.files_kardex = [];
      this.files_kardexB64 = null;
    },
    deleteFilesPlanillaUnaVotacion() {
      this.files_planilla_urna_votacion = [];
      this.this.files_planilla_urna_votacionB64 = null;
    },
    deleteFilesCartaMilitancia() {
      this.files_carta_militancia = [];
      this.files_carta_militanciaB64 = null;
    },

    //CONVERTIR A BASE64 VICEPRESIDENCIA EJECUTIVA
    //credencial
    handlerCredeV(value, index) {
      if (value[0].size < 500000) {
        this.createBase64CredeV(value[0].file, index);
      } else {
        this.vicepresidencia_ejecutiva[index].files_credencial = [];
        this.$toast.open({
          message: "Su archivo excede los 500 KB!",
          type: "warning",
          position: "bottom-right",
          duration: 5000,
        });
      }
    },
    createBase64CredeV(file, index) {
      const reader = new FileReader();
      reader.onload = (e) => {
        this.vicepresidencia_ejecutiva[index].files_credencialB64 =
          e.target.result;
      };
      reader.readAsDataURL(file);
    },

    //kardex
    handlerKardexV(value, index) {
      if (value[0].size < 500000) {
        this.createBase64KardexV(value[0].file, index);
      } else {
        this.vicepresidencia_ejecutiva[index].files_kardex = [];
        this.$toast.open({
          message: "Su archivo excede los 500 KB!",
          type: "warning",
          position: "bottom-right",
          duration: 5000,
        });
      }
