      };
      reader.readAsDataURL(file);
    },
    //Borrar documentos de la vicepresidencias ejecutivas
    deleteFilesCredencialVE(index) {
      this.vicepresidencia_ejecutiva[index].files_credencial = [];
      this.vicepresidencia_ejecutiva[index].files_credencialB64 = null;
    },
    deleteFilesKardexVE(index) {
      this.vicepresidencia_ejecutiva[index].files_kardex = [];
      this.vicepresidencia_ejecutiva[index].files_kardexB64 = null;
    },
    // activacin de primera fase
    async getPrimeraVez() {
      let first = await axios.get(
        `${apiURL}getFirst`
      );
      this.loading = false;
      // console.log(first.data.data[0][0].primera_vez)
      this.primera_vez = first.data.data[0][0].primera_vez;
    }
  },
  mounted() {
    this.codigo_presidente = this.$route.params.codigo;
    if (this.$route.params.nivel === 1) {
      this.nivel_presidente = "Preparatoria";
    } else {
      this.nivel_presidente = "Licenciatura";
    }
    this.id_folio = this.$route.params.id_folio;
    this.eleccion_id = this.$route.params.eleccion_id;
    this.nombre_presidente = this.$store.state.nombre_presidente;
    this.getPrimeraVez();
    this.getSexo();
    this.getGenero();
    this.postObtenerCentrosUniversitarios();
    this.getGruposPoliticos();
    this.getTurnos();
    eventBus.$on("guardar", () => {
      this.postGuardar();
    });
  },
};
</script>
