import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loading: {
      titulo: '',
      estado: false
    },
    nombre_presidente: ''
  },
  mutations: {
    ocultarLoad(state) {
      state.loading.estado = false
    },
    mostrarLoad(state, payload) {
      state.loading.titulo = payload.titulo
      state.loading.estado = true
    },
    setNombrePresidente(state, nombre_presidente) {
      state.nombre_presidente = nombre_presidente
    }
  },
  actions: {
  },
  modules: {
  }
})
