<template>
  <v-container class="leblanc mb-6">
    <v-row no-gutters>
      <v-col cols="12" sm="12" md="12">
        <p class="text-center text-h5 mt-10">
          Pgina para registrarse a candidatura de presidencia
        </p>
      </v-col>
      <v-col cols="12" md="12">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-container>
            <v-row>
              <v-col cols="12" md="12">
                <p class="text-left mt-10">
                  <v-avatar color="grey darken-3" size="36"
                    ><v-icon small dark>fas fa-id-badge</v-icon></v-avatar
                  >
                  Informacin sobre el registro
                </p>
              </v-col>
              <v-col cols="12" md="8" class="text-left">
                <label>Nombre completo</label>
                <v-text-field
                  v-model="nombre"
                  :rules="nombre_completoRules"
                  label=""
                  outlined
                  required
                ></v-text-field>
              </v-col>

              <v-col cols="12" md="4" class="text-left">
                <label>Nivel</label>
                <v-select
                  v-model="nivel"
                  :items="niveles"
                  outlined
                  eager
                  item-text="label"
                  item-value="value"
                  :loading="loading"
                  attach
                  :rules="nivelRules"
                ></v-select>
              </v-col>

              <v-col cols="12" md="4" class="text-left">
                <label>Correo electrnico</label>
                <v-text-field
                  v-model="correo"
                  :rules="emailRules"
                  label=""
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="4" class="text-left"
                ><label>Cdigo U de G</label>
                <v-text-field
                  v-model="codigo"
                  :rules="codigoUGRules"
                  label=""
                  outlined
                  required
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <p class="text-left mt-2">
                  <v-avatar color="grey darken-3" size="36"
                    ><v-icon small dark>fas fa-users</v-icon></v-avatar
                  >
                  Perteneces a alguna coalicin?
                </p>
              </v-col>
              <v-col cols="12" sm="12" md="12">
                <v-radio-group
                  v-model="coalicion"
                  :rules="coalicionRules"
                  @change="validarCoalicionTarjetas"
                  row
                >
                  <v-radio
                    label="S"
                    value="si"
                  ></v-radio>
                  <v-radio v-if="primera_vez"
                    label="No"
                    value="no"
                  ></v-radio>
                </v-radio-group>
              </v-col>
              <template v-if="!primera_vez">
                <v-col cols="12" sm="12" md="12">
                  <p class="text-left mt-2">
                    Ingresa los folios de cada persona registrada en el esquema
                    de coalicin
                  </p>
                </v-col>
                <v-col cols="12" md="4" class="text-left">
                  <label>Folio</label>
                  <v-text-field
                    v-model="folios[0]"
                    :rules="foliosRules"
                    label=""
                    outlined
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4" class="text-left">
                  <label>Folio</label>
                  <v-text-field
                    v-model="folios[1]"
                    :rules="foliosRules"
                    label=""
                    outlined
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4" class="text-left">
                  <label>Folio</label>
                  <v-text-field
                    v-model="folios[2]"
                    label=""
                    outlined
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4" class="text-left">
                  <label>Folio</label>
                  <v-text-field
                    v-model="folios[3]"
                    label=""
                    outlined
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4" class="text-left">
                  <label>Folio</label>
                  <v-text-field
                    v-model="folios[4]"
                    label=""
                    outlined
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4" class="text-left">
                  <label>Folio</label>
                  <v-text-field
                    v-model="folios[5]"
                    label=""
                    outlined
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <p class="text-left mt-2">
                    <v-avatar color="grey darken-3" size="36"
                      ><v-icon small dark>fas fa-file-image</v-icon></v-avatar
                    >
                    Selecciona el grupo poltico que representas
                  </p>
                </v-col>
              </template>
              <!-- Tarjeta de los grupos politicos que representan -->
              <!-- :cols="card.flex" gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)" -->
              <v-col v-for="card in cards" :key="card.title" sm="12" md="2">
                <v-card width="450" height="200">
                  <div
                    class="
                      d-flex
                      flex-column
                      justify-space-between
                      align-center
                    "
                  >
                    <v-img
                      v-if="
                        card.title === 'IMPULSO' || card.title === 'SETUMISMO'
                      "
                      :src="card.src"
                      contain
                      class="white--text align-end mt-0"
                      aspect-ratio="16/9"
                      height="135px"
                      width="260"
                      max-height="135"
                    >
                      <template v-slot:placeholder>
                        <v-row
                          class="fill-height ma-0"
                          align="center"
                          justify="center"
                        >
                          <v-progress-circular
                            indeterminate
                            color="primary"
                          ></v-progress-circular>
                        </v-row>
                      </template>
                      <!-- <v-card-title v-text="card.title"></v-card-title> -->
                    </v-img>
                    <v-img
                      v-else
                      :src="card.src"
                      contain
                      class="white--text align-end mt-6"
                      aspect-ratio="16/9"
                      height="110px"
                      width="200"
                    >
                      <template v-slot:placeholder>
                        <v-row
                          class="fill-height ma-0"
                          align="center"
                          justify="center"
                        >
                          <v-progress-circular
                            indeterminate
                            color="primary"
                          ></v-progress-circular>
                        </v-row>
                      </template>
                      <!-- <v-card-title v-text="card.title"></v-card-title> -->
                    </v-img>
                  </div>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-checkbox
                      v-model="grupopolitico"
                      :value="card"
                      @change="validarCoalicion(card.id)"
                    ></v-checkbox>
                  </v-card-actions>
                </v-card>
              </v-col>
              <!-- de las tarjetas -->
              <v-col cols="12" sm="12" md="12">
                <v-btn
                  :disabled="!valid"
                  depressed
                  color="primary"
                  x-large
                  width="250"
                  @click="validate"
                >
                  Registrarse
                </v-btn>
              </v-col>
              <!-- <v-col
      cols="12"
      sm="12"
      md="12"
      >
      <p class="text-center" style="font-size: 0.75rem;">
        Recuperar clave de acceso <v-icon small>fas fa-arrow-right</v-icon>
      </p>
    </v-col> -->
            </v-row>
          </v-container>
        </v-form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapMutations } from "vuex";
import axios from "axios";
import {apiURL} from '../util/api'
export default {
  name: "Registro",
  data: () => ({
    loading: false,
    primera_vez: null,
    eleccion_id: null,
    valid: true,
    niveles: [],
    nombre: null,
    nivel: null,
    correo: null,
    codigo: null,
    coalicion: null,
    temporal: null,
    folios: [],
    grupopolitico: [],
    grupopolitico_id: [],
    grupopolitico_nombres: [],
    nombre_completoRules: [
      (v) => !!v || "Nombre completo es requerido",
      (v) =>
        (v && v.length >= 2) ||
        "Nombre completo debe tener ms de 2 caracteres",
    ],
    emailRules: [
      (v) => !!v || "Correo electrnico es requerido",
      (v) => /.+@.+/.test(v) || "Correo electrnico debe ser vlido",
    ],
    codigoUGRules: [
      (v) => !!v || "Cdigo U de G es requerido",
      (v) =>
        (v && v.length >= 5) || "Cdigo U de G debe tener ms de 2 caracteres",
      (v) => /^\d*$/.test(v) || "Solo nmeros",
    ],
    coalicionRules: [(v) => !!v || "Coalicin es requerida"],
    nivelRules: [(v) => !!v || "Nivel es requerido"],
    foliosRules: [(v) => !!v || "Folio es requerido"],
    cards: [],
  }),
  methods: {
    ...mapMutations(["ocultarLoad", "mostrarLoad"]),
    async getGruposPoliticos() {
      let grupos_politicos = await axios.get(
        `${apiURL}obtainpoliticalgroups`
      );
      let gp = grupos_politicos.data.data;
      Object.keys(gp).forEach((element) => {
        this.cards.push({
          id: gp[element].id,
          title: gp[element].nombre,
          src: require("../assets/" + gp[element].imagen + ""),
        });
      });
      this.cards = this.cards.sort(function (a, b) {
        if (a.title > b.title) {
          return 1;
        }
        if (a.title < b.title) {
          return -1;
        }
        // a must be equal to b
        return 0;
      });
    },
    async getEleccionId() {
      let id_eleccion = await axios.get(
        `${apiURL}obtainactiveelection`
      );
      //console.log(id_eleccion.data.data[0].eleccion_id)
      this.eleccion_id = id_eleccion.data.data[0].eleccion_id;
    },
    async getObtenerNiveles() {
      this.loading = true;
      let niveles = await axios.get(
        `${apiURL}obtainnivels`
      );
      this.loading = false;
      let lista_niveles = niveles.data.data;
      Object.keys(lista_niveles).forEach((element) => {
        this.niveles.push({
          value: lista_niveles[element].nivel,
          label: lista_niveles[element].nivel,
        });
      });
    },
    /* validate () {
      this.$refs.form.validate()
    },*/
    async validate() {
      let valido = await this.$refs.form.validate();
      if (valido) {
        let nivel_id;
        if (this.nivel === "Preparatoria") {
          nivel_id = 1;
        } else {
          nivel_id = 2;
        }
        try {
          Object.keys(this.grupopolitico).forEach((element) => {
            if (typeof this.grupopolitico[element].id !== "undefined") {
              this.grupopolitico_id.push(this.grupopolitico[element].id);
            }
            if (typeof this.grupopolitico[element].title !== "undefined") {
              this.grupopolitico_nombres.push(
                this.grupopolitico[element].title
              );
            }
          });
          let url;
          if(this.primera_vez) {
            url = `${apiURL}registerStep1`
          } else {
            url = `${apiURL}registerFaseDosStep1`
          }
          this.mostrarLoad({ titulo: "Enviando datos, por favor espere" });
          this.$store.commit("setNombrePresidente", this.nombre);
          let respuesta = await axios.post(url,
            {
              nombre: this.nombre,//String
              nivel: nivel_id,//String
              correo: this.correo,//String
              codigo: this.codigo,//String
              coalicion: this.coalicion,//String
              temporal: this.temporal,//String
              folios: this.folios,//Array
              grupopolitico: this.grupopolitico_id,//Array
              eleccion_id: this.eleccion_id,//String
            },
            {headers: {'X-Requested-With': 'XMLHttpRequest', 'Cache-Control': 'no-cache', 'Content-Type': 'application/json'}}
          );
          // console.log(respuesta.data.data)
          if (respuesta.data.data[0]=== "este correo ya fue asociado.") {
            let upper = respuesta.data.data[0].toUpperCase();
            this.$toast.open({
              message: upper,
              type: "error",
              position: "bottom-right",
              duration: 5000,
            });
          } else if(respuesta.data.data === "folio invalido") {

            this.$toast.open({
              message: `Uno o ms folios son invlidos. Favor de verificar. (${respuesta.data.folios})`,
              type: "error",
              position: "bottom-right",
              duration: 20000,
            });
          } else {
            this.$router.push({
              name: "Registro02",
              params: {
                eleccion_id: this.eleccion_id,
                codigo: this.codigo,
                nivel: nivel_id,
                id_folio: respuesta.data.data[0]
              },
            });
          }
        } catch (ex) {
          console.error("inner", ex.message);
          this.grupopolitico_id = []
          this.grupopolitico_nombres = []
          this.ocultarLoad();
          this.$toast.open({
          message: 'No se pudo procesar la informacin, por favor intentelo ms tarde!',
          type: "error",
          position: "bottom-right",
          duration: 5000,
        });
        } finally {
          this.$refs.form.resetValidation();
          this.ocultarLoad();
          console.log("finally");
        }
      } else {
        //this.$refs.form.resetValidation()
        this.$toast.open({
          message: "LOS CAMPOS NO PUEDEN ESTAR VACIOS!",
          type: "error",
          position: "bottom-right",
          duration: 5000,
        });
      }
    },
    validarCoalicion(val) {
      if (this.coalicion == "si") {
        this.grupopolitico.push(val);
      } else {
        if (this.grupopolitico.length <= 1) {
          //console.log(val)
        } else {
          this.grupopolitico.pop();
        }
      }
    },
    validarCoalicionTarjetas() {
      if (this.grupopolitico.length > 1) {
        this.grupopolitico = [];
      }
    },
    async getPrimeraVez() {
      let first = await axios.get(
        `${apiURL}getFirst`
      );
      this.loading = false;
      // console.log(first.data.data[0][0].primera_vez)
      this.primera_vez = first.data.data[0][0].primera_vez;
      if(!this.primera_vez) {
        this.coalicion = "si"
      }
    }
  },
  mounted() {
    this.getPrimeraVez();
    this.getGruposPoliticos();
    this.getEleccionId();
    this.getObtenerNiveles();
  },
};
</script>