<script>
import { mapMutations } from "vuex";
import axios from "axios";
import { apiURL } from "../../util/api";
//import { Base64 } from "js-base64";
//import FormPlanilla from "@/components/FormPlanilla.vue";
import DialogDocumento from "@/components/DialogDocumento.vue";
import { ExportToCsv } from "export-to-csv";
export default {
  name: "AdminPrimeraFase",
  components: {
    'FormPlanilla': () => import('@/components/FormPlanilla.vue'),
    DialogDocumento
  },
  data: () => ({
    locacion: null,
    user: {},
    motivo: null,
    modal: false,
    dialog_export: false,
    vicepresidencia_ejecutiva_visible: false,
    loading: false,
    buscar: null,
    eleccion_id: null,
    niveles: [],
    grupos_politicos: [],
    centros: [],
    divisiones: [],
    turnos: [],
    folio: null,
    nombre: null,
    nivel: null,
    centro: null,
    division: null,
    turno: null,
    postulacion: [],
    password: "",
    passwordRules: [
      v => !!v || "Contrasea es requerida",
      v => (v && v.length >= 8) || "Minmo 8 caracteres"
    ],
    email: "",
    emailRules: [
      v => !!v || "Correo electrnico es requerido",
      v => /.+@.+\..+/.test(v) || "Correo electrnico debe ser vlido"
    ],
    resultados: [],
    tipo_datos_exportar: null,
    tipo_csv_exportar: null,
    loading_export: false,
    file_existe: null,
    file_export: null,
    dialog_eliminar: false,
    folio_eliminar: null,
    // DataTable Paginacin
    totalDesserts: 0,
    desserts: [],
    options: {
      itemsPerPage: 10
    },
    headers: [
      {
        text: "Folio",
        align: "start",
        sortable: false,
        value: "folio"
      },
      { text: "Presidencia", value: "nombre", sortable: false },
      {
        text: "Grupo Poltico",
        value: "corriente_politica_presidente",
        sortable: false
      },
      { text: "", value: "division_presidencia", sortable: false },
      { text: "Turno", value: "turno_presidente", sortable: false },
      { text: "Estatus", value: "estatus", sortable: false },
      { text: "", value: "acciones", sortable: false }
    ],
    // Dialogo para editar
    openDialogoForm: false,
    dataBasicaEditar: {},
    // Dialogo para visualizar los documentos
    openDialogoDocument: false,
    dataVisualizar: null,
    tipoArchivo: null
  }),
  mounted() {
    /* this.locacion = window.location;
    let existe = this.$session.exists("token");
    if (!existe) {
      this.$router.push({ name: "IndexAdmin" });
    } */
    this.user = this.$session.get("user");
    this.getEleccionId();
    this.getObtenerNiveles();
    this.getTurnos();
    this.getGruposPoliticos();
  },
  watch: {
    options: {
      handler() {
        this.getDataFromApi();
      },
      deep: true
    }
  },
  methods: {
    ...mapMutations(["ocultarLoad", "mostrarLoad"]),
    getDataFromApi() {
      this.loading = true;
      this.resultados = [];
      this.resultados = [];
      let nivel_id;
      if (this.nivel === "Preparatoria") {
        nivel_id = 1;
      } else if (this.nivel === "Universidad") {
        nivel_id = 2;
      } else {
        nivel_id = null;
      }

      if (this.folio === "") {
        this.folio = null;
      }

      const { page } = this.options;

      axios
        .get(`${apiURL}getRegister`, {
          params: {
            folio: this.folio,
            nombre: this.nombre,
            nivel: nivel_id,
            centro_presidencia: this.centro,
            division_presidencia: this.division,
            turno_presidente: this.turno,
            pagina: page
          }
        })
        .then(response => {
          // console.log(response.data.data.totalPages);
          this.totalDesserts = response.data.data.count;
          this.desserts = response.data.data.docs;
          this.loading = false;
        })
        .catch(error => {
          console.error(error);
          this.loading = false;
          this.$toast.open({
            message:
              "No se pudo procesar la informacin, por favor intentelo ms tarde!",
            type: "error",
            position: "bottom-right",
            duration: 5000
          });
        });
    },
    async getEleccionId() {
      let id_eleccion = await axios.get(`${apiURL}obtainactiveelection`);
      //console.log(id_eleccion.data.data[0].eleccion_id)
      this.eleccion_id = id_eleccion.data.data[0].eleccion_id;
    },
    async getGruposPoliticos() {
      let grupospoliticos = await axios.get(`${apiURL}obtainpoliticalgroups`);
      this.grupos_politicos = grupospoliticos.data.data;
    },
    async getObtenerNiveles() {
      this.loading = true;
      let niveles = await axios.get(`${apiURL}obtainnivels`);
      this.loading = false;
      //console.log(`Niveles: ${niveles.data} `)
      let lista_niveles = niveles.data.data;
      Object.keys(lista_niveles).forEach(element => {
        this.niveles.push({
          value: lista_niveles[element].nivel,
          label: lista_niveles[element].nivel
        });
      });
    },
    async postObtenerCentrosUniversitarios() {
      this.loading = true;
      this.divisiones = [];
      this.centros = [];
      this.division = null;
      this.centro = null;
      this.turno = null;
      let respuesta = await axios.post(`${apiURL}obtaincentros`, {
        eleccion_id: this.eleccion_id
      });
      this.loading = false;
      let lista_centros = respuesta.data.data;
      Object.keys(lista_centros).forEach(element => {
        //console.log( lista_niveles[element].nivel);
        this.centros.push({
          value: lista_centros[element].centro,
          label: lista_centros[element].centro_desc
        });
      });
      //Se comprueba si es preparatoria
      if (this.nivel === "Preparatoria") {
        this.centros = this.centros.filter(function(centro) {
          return centro.value === "SEMS";
        });
      } else {
        this.centros = this.centros.filter(function(centro) {
          return centro.value !== "SEMS";
        });
      }
    },
    async postObtenerDivisiones() {
      this.loading = true;
      this.divisiones = [];
      let respuesta = await axios.post(`${apiURL}obtaindivision`, {
        eleccion_id: this.eleccion_id,
        centro: this.centro
      });
      this.loading = false;
      let lista_divisiones = respuesta.data.data;
      Object.keys(lista_divisiones).forEach(element => {
        //console.log( lista_niveles[element].nivel);
        this.divisiones.push({
          value: lista_divisiones[element].plantel,
          label: lista_divisiones[element].plantel
        });
      });
    },
    async getTurnos() {
      this.loading = true;
      let turno = await axios.get(`${apiURL}obtainturns`);
      this.loading = false;
      let tn = turno.data.data;
      Object.keys(tn).forEach(element => {
        //console.log( gn[element].genero);
        this.turnos.push(tn[element].turno);
      });
    },
    //Modal para visualizar los datos
    async modalVisualizacion(item, estatus) {
      if (estatus) {
        this.mostrarLoad({ titulo: "Por favor espere" });
        let resultados = await axios.get(`${apiURL}getAllData`, {
          params: {
            folio: item
          }
        });
        this.ocultarLoad();
        // console.log(resultados.data.data[0][0]);
        this.postulacion = resultados.data.data[0][0];
        this.postulacion.estatus = estatus;
        if (
          this.postulacion.corriente_politica_presidente &&
          this.postulacion.grupo_politico_vicepresidencia
        ) {
          this.postulacion.corriente_politica_presidente = this.buscarNombreGrupoPolitico(
            this.postulacion.corriente_politica_presidente
          );
          this.postulacion.grupo_politico_vicepresidencia = this.buscarNombreGrupoPolitico(
            this.postulacion.grupo_politico_vicepresidencia
          );
          if (this.postulacion.vicepresidencia_ejecutiva[0].codigo) {
            this.vicepresidencia_ejecutiva_visible = true;
          } else {
            this.vicepresidencia_ejecutiva_visible = false;
          }
          this.modal = true;
        } else {
          this.$toast.open({
            message: "Disculpe el registro no esta completo!",
            type: "error",
            position: "bottom-right",
            duration: 5000
          });
        }
      }
    },
    async getBuscar(folio) {
      this.mostrarLoad({ titulo: "Por favor espere" });
      try {
        let resultados = await axios.get(
          `${apiURL}getAllData`,
          {
            params: {
              folio: folio
            }
          }
        );
        this.ocultarLoad();
        console.log(resultados.data.data[0][0]);
        if (resultados.data.data[0][0].length !== 0) {
          this.resultados = resultados.data.data[0][0];
          delete this.resultados.files_fotografia
          delete this.resultados.files_carta_militancia
          delete this.resultados.files_carta_no_violencia
          delete this.resultados.files_credencial
          delete this.resultados.files_kardex
          delete this.resultados.files_plan_trabajo
          delete this.resultados.files_planilla_urna_votacion
          if(this.resultados.vicepresidencia_ejecutiva === null) {
            this.resultados.vicepresidencia_ejecutiva = []
          } else {
            if(this.resultados.vicepresidencia_ejecutiva.length > 0 ) {
              if(this.resultados.vicepresidencia_ejecutiva[0].nombre === '') {
                this.resultados.vicepresidencia_ejecutiva = []
              }
            }
          }
          console.log(this.resultados)
          return this.resultados;
        } else {
          this.$toast.open({
            message: "No se encontraron resultados!",
            type: "error",
            position: "bottom-right",
            duration: 5000
          });
        }
      } catch (ex) {
        console.error("inner", ex.message);
        this.ocultarLoad();
        this.$toast.open({
          message:
            "No se pudo procesar la informacin, por favor intentelo ms tarde!",
          type: "error",
          position: "bottom-right",
          duration: 5000
        });
      } finally {
        this.ocultarLoad();
        console.log("finally");
      }
    },
    buscarNombreGrupoPolitico(id) {
      if (id) {
        let grupo_politico = this.grupos_politicos.find(
          grupo => grupo.id === id
        );
        return grupo_politico.nombre;
      } else {
        return "";
      }
    },
    buscarNombreCentro(centro) {
      if (centro) {
        let nombre_centro = this.centros.find(
          centrof => centrof.value === centro
        );
        return nombre_centro.label;
      } else {
        return "";
      }
    },
    async updateEstatus(estatus) {
      if (estatus !== "Rechazado") {
        this.mostrarLoad({ titulo: "Por favor espere" });
        await axios.post(`${apiURL}changeEstatus`, {
          folio: this.postulacion.folio,
          estatus: estatus,
          // motivo: this.motivo
        });
        this.ocultarLoad();
        this.postulacion.estatus = estatus;
        this.getDataFromApi();
      } else {
        if (this.motivo) {
          this.mostrarLoad({ titulo: "Por favor espere" });
          await axios.post(`${apiURL}changeEstatus`, {
            folio: this.postulacion.folio,
            estatus: estatus,
            motivo: this.motivo
          });
          this.ocultarLoad();
          this.postulacion.estatus = estatus;
          this.getDataFromApi();
        } else {
          this.$toast.open({
            message: "Disculpe debe indicar el motivo!",
            type: "error",
            position: "bottom-right",
            duration: 5000
          });
        }
      }
    },
    async exportDataAprobadoRechazado() {
      if (this.tipo_datos_exportar && this.tipo_csv_exportar) {
        this.loading_export = true;
        try {
          let tipo = null;
          let url = null;
          if (this.tipo_csv_exportar === "csv_normal") {
            url = `${apiURL}exportAcepted`
            tipo = true;
          } else {
            tipo = false;
            url = `${apiURL}getMovil`
          }
          let resultados = await axios.get(url, {
            params: {
              estatus: this.tipo_datos_exportar
            }
          });
          this.loading_export = false;
          this.file_existe = resultados.data.data;
          console.log(tipo);
          console.log(resultados.data.data);
          //Base64.extendString();
          //this.file_export = Base64.decode(resultados.data.data.docs);
          const options = {
            fieldSeparator: ",",
            quoteStrings: '"',
            decimalSeparator: ".",
            showLabels: true,
            showTitle: true,
            filename: this.tipo_datos_exportar,
            // title: `${this.tipo_datos_exportar}`,
            useTextFile: false,
            useBom: true,
            useKeysAsHeaders: true
            // headers: ['Column 1', 'Column 2', etc...] <-- Won't work with useKeysAsHeaders present!
          };

          const csvExporter = new ExportToCsv(options);

          await csvExporter.generateCsv(this.file_existe);
        } catch (ex) {
          console.error("inner", ex);
          this.ocultarLoad();
          this.$toast.open({
            message:
              "No se pudo procesar la informacin, por favor intentelo ms tarde!",
            type: "error",
            position: "bottom-right",
            duration: 5000
          });
        } finally {
          this.ocultarLoad();
          console.log("finally");
        }
        //
      } else {
        this.$toast.open({
          message: "Disculpe debe seleccionar una opcin!",
          type: "error",
          position: "bottom-right",
          duration: 5000
        });
      }

      //
    },
    modalEliminar(item) {
      this.dialog_eliminar = true;
      this.folio_eliminar = item.folio;
    },
    async deleteFolio(folio) {
      this.mostrarLoad({ titulo: "Por favor espere" });
      let resultados = await axios.delete(`${apiURL}deleteRegister`, {
        data: { folio: folio }
      });
      if (resultados.data.data === "Registro eliminado") {
        this.getDataFromApi();
        this.ocultarLoad();
        this.dialog_eliminar = false;
        this.$toast.open({
          message: "Registro eliminado con xito!",
          type: "success",
          position: "bottom-right",
          duration: 5000
        });
      } else {
        this.ocultarLoad();
        this.$toast.open({
          message: "Disculpe ocurrio un error!",
          type: "error",
          position: "bottom-right",
          duration: 5000
        });
      }
    },
    //Modal Dialogo Editar
    async modalDialogoEditar(item) {
      let resultados = await this.getBuscar(item.folio)
      this.dataBasicaEditar = await resultados;
      this.openDialogoForm = true;
    },
    cerrarModalDiaEdiForm(val) {
      this.openDialogoForm = val;
      this.dataBasicaEditar = {};
    },
    // Modal Dialogo para Visualizar los Documentos
    modalDialogoVisualizar(item) {
      this.tipoArchivo = item.split(";")[0].split("/")[0];
      console.log(this.tipoArchivo);
      this.dataVisualizar = item;
      this.openDialogoDocument = true;
    },
    cerrarModalDialogVisualizar(val) {
      this.openDialogoDocument = val;
      this.dataVisualizar = null;
      this.tipoArchivo = null;
    }
  }
};
</script>

